@font-face {
  font-family: 'font-fileuploader';
  src: url('../fonts/fileuploader/font-fileuploader.eot?l2vub3');
  src: url('../fonts/fileuploader/font-fileuploader.eot?l2vub3#iefix') format('embedded-opentype'),
  url('../fonts/fileuploader/font-fileuploader.ttf?l2vub3') format('truetype'),
  url('../fonts/fileuploader/font-fileuploader.woff?l2vub3') format('woff'),
  url('../fonts/fileuploader/font-fileuploader.svg?l2vub3#font-fileuploader') format('svg');
  font-weight: normal;
  font-style: normal;
}

// Variables
$fileuploader-distance: 16px;
$fileuploader-background: #fafbfd;
$fileuploader-theme-color-1: #3A8FFE;
$fileuploader-theme-color-2: #9658FE;
$fileuploader-color-active: #789BEC;
$fileuploader-color-error: #FE7676;
$fileuploader-color-success: #43D084;
$fileuploader-color-action: #c0c6d2;
$fileuploader-borderRadius: 6px;

$fileuploader-button-background: #e6ebf4;
$fileuploader-button-color: #90a0bc;

$fileuploader-input-caption-color: #789BEC;
$fileuploader-input-caption-background: #fff;
$fileuploader-input-caption-border: 1px solid #ebeef1;

$fileuploader-item-borderBottom: $fileuploader-input-caption-border;
$fileuploader-item-color: #74809D;
$fileuploader-item-mutedColor: #979fb8;
$fileuploader-item-actionSize: 20px;
$fileuploader-item-thumbnailSize: 36px;

$fileuploader-popup-zIndex: 1090;
$fileuploader-popup-padding: 34px;
$fileuploader-popup-contentWidth: 368px;
$fileuploader-popup-content-color: #5B5B7B;
$fileuploader-popup-content-mutedColor: #ACACC4;
$fileuploader-popup-move-width: 56px;
$fileuploader-cropper-pointWidth: 24px;

// SVG Images
$icon-loader: "";
$icon-file: "";

// Mixins
@mixin gradient-background($from, $to, $degrees: -45deg) {
  background: $to;
  background: -moz-linear-gradient($degrees, $from 0%, $to 100%);
  background: -webkit-linear-gradient($degrees, $from 0%, $to 100%);
  background: linear-gradient(180deg + $degrees, $from 0%, $to 100%);
}

// Extends
%fileuploader-transition-1 {
  -webkit-transition: all .1s ease;
  transition: all .1s ease;
}

%fileuploader-transition-2 {
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

%fileuploader-animation {
  -webkit-animation: fileuploaderFadeIn 0.2s ease;
  animation: fileuploaderFadeIn 0.2s ease;
}

%fileuploader-button {
  display: inline-block;
  margin: 0;
  padding: 14px 20px;
  border: none;
  border-radius: $fileuploader-borderRadius;
  outline: 0;
  font-weight: bold;
  cursor: pointer;
  vertical-align: middle;
  text-decoration: none;
  @extend %fileuploader-transition-2;
  @extend %user-select-none;
}

%fileuploader-button-default {
  @extend %fileuploader-button;
  background: $fileuploader-button-background;
  color: $fileuploader-button-color;

  &:hover {
    background: lighten($fileuploader-button-background, 2%);
  }
  &:active {
    background: darken($fileuploader-button-background, 2%);
  }
}

%fileuploader-button-theme {
  @extend %fileuploader-button;
  @include gradient-background($fileuploader-theme-color-1, $fileuploader-theme-color-2);
  background-size: 140% auto;
  background-position: center;
  color: #fff;

  &:hover {
    background-position: left;
  }
  &:active {
    background-position: right;
  }
}

%fileuploader-icon {
  font-family: 'font-fileuploader' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

%fileuploader-loader {
  content: '';
  position: absolute;
  min-width: 24px;
  min-height: 24px;
  max-width: 48px;
  max-height: 48px;
  background: url('data:image/svg+xml;base64,#{$icon-loader}') no-repeat center;
  background-size: contain;
}

%transform-to-center {
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

%transform-to-vertical-center {
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

%user-select-none {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* configuration */
.fileuploader,
.fileuploader *,
.fileuploader :before,
.fileuploader :after,
.fileuploader-popup,
.fileuploader-popup *,
.fileuploader-popup :before,
.fileuploader-popup :after {
  box-sizing: border-box;
}

/* main */
.fileuploader {
  display: block;
  width: 100%;
  padding: $fileuploader-distance;
  margin: $fileuploader-distance 0;
  background: $fileuploader-background;
  border-radius: $fileuploader-borderRadius;
  line-height: normal;
}

/* input */
.fileuploader-input {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid transparent;
  border-radius: $fileuploader-borderRadius;
  cursor: pointer;
  overflow: hidden;

  .fileuploader-input-caption {
    position: relative;
    display: inline-block;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    -ms-flex-item-align: start;
    align-self: flex-start;

    padding: 11px $fileuploader-distance;
    margin-right: $fileuploader-distance;
    background: $fileuploader-input-caption-background;
    border: $fileuploader-input-caption-border;
    border-radius: $fileuploader-borderRadius;
    color: $fileuploader-input-caption-color;
    font-weight: bold;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    @extend %fileuploader-transition-2;

    .fileuploader-focused & {
      border-color: $fileuploader-input-caption-color;
    }

    /*
    .fileuploader.fileuploader-is-uploading & {
        padding-left: 40px;

        &:after {
            @extend %fileuploader-loader;
            left: 10px;
            top: 50%;
            margin-top: -12px;
        }
    }
    */

    .fileuploader-pending-loader {
      @extend %fileuploader-loader;
      position: relative;
      display: inline-block;
      vertical-align: middle;
      min-height: 22px;
      margin-right: 6px;
    }
  }

  .fileuploader-input-button {
    @extend %fileuploader-button-theme;
    padding: 12px 18px;
  }

  .fileuploader-main-icon {
    color: $fileuploader-color-active;

    &:after {
      @extend %fileuploader-icon;
      content: "\e900";
    }
  }

  &.fileuploader-dragging {
    background: #fff;
    border: $fileuploader-input-caption-border;
    border-style: dashed;

    .fileuploader-input-caption {
      border-color: transparent;
    }
    .fileuploader-input-button {
      -webkit-transform: translateX(100%);
      transform: translateX(100%);
      opacity: 0;
    }
  }

  .fileuploader-disabled & {
    opacity: 0.7;
    cursor: default;
    pointer-events: none !important;
  }
}

/* items */
.fileuploader-items {
  .fileuploader-items-list {
    display: block;
    margin: 0 $fileuploader-distance*-1;
    padding: 0;
    list-style: none;
  }

  .fileuploader-item {
    position: relative;
    margin: 0;
    padding: 20px $fileuploader-distance;
    border-bottom: $fileuploader-item-borderBottom;
    @extend %fileuploader-animation;
    animation-duration: 0.6s;

    &:last-child {
      border-bottom: 0;
      margin-bottom: $fileuploader-distance*-1;
    }

    &.upload-failed {
      background: rgba(254, 84, 111, 0.06);
    }

    &.upload-pending .fileuploader-action-remove:after {
      @extend %fileuploader-loader;
      position: absolute;
      left: -8px;
      top: -8px;
      width: 36px;
      height: 36px;
    }

    .columns {
      position: relative;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      z-index: 2;
    }

    .column-thumbnail {
      position: relative;
      width: $fileuploader-item-thumbnailSize;
      height: $fileuploader-item-thumbnailSize;
    }

    .column-title {
      -webkit-box-flex: 1;
      -ms-flex: 1;
      flex: 1;
      padding-left: $fileuploader-distance;
      padding-right: $fileuploader-distance;
      color: $fileuploader-item-color;
      overflow: hidden;

      a {
        display: inline-block;
        width: 100%;
        height: 100%;
        color: $fileuploader-item-color;
        text-decoration: none;
      }

      div {
        width: 100%;
        font-weight: bold;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }

      span {
        font-size: 12px;
        color: $fileuploader-item-mutedColor;
      }
    }

    .column-actions {
      margin: 0 $fileuploader-distance;
    }

    .fileuploader-item-image {
      position: relative;
      width: 100%;
      height: 100%;
      border-radius: $fileuploader-borderRadius;
      background: #fff;
      overflow: hidden;

      &.fileuploader-loading {
        &:after {
          content: '';
          @extend %fileuploader-loader;
          @extend %transform-to-center;

          width: 50%;
          height: 50%;
        }
      }

      img,
      canvas {
        position: absolute;
        max-width: none;
        max-height: 100%;

        @extend %transform-to-center;
        @extend %fileuploader-animation;
      }
    }

    .fileuploader-item-icon {
      position: relative;
      width: 100%;
      height: 100%;
      text-align: center;
      color: #fff;
      font-size: 11px;
      background: #ddd;
      border-radius: 4px;
      cursor: default;
      @extend %user-select-none;
      @extend %fileuploader-animation;

      &.is-bright-color {
        color: #888;
      }

      i {
        position: absolute;
        display: block;
        width: 90%;
        font-style: normal;
        font-weight: bold;
        overflow: hidden;
        white-space: nowrap;
        @extend %transform-to-center;
      }
    }

    span.fileuploader-action-popup {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(43, 56, 71, 0.2);
      border-radius: $fileuploader-borderRadius;
      cursor: pointer;
      opacity: 0;

      @extend %fileuploader-transition-2;

      &:hover {
        opacity: 1;
      }

      &:active {
        background: rgba(43, 56, 71, 0.4);
      }
    }

    .fileuploader-action {
      position: relative;
      display: inline-block;
      width: $fileuploader-item-actionSize;
      height: $fileuploader-item-actionSize;
      color: $fileuploader-color-action;
      cursor: pointer;
      vertical-align: top;

      @extend %transform-to-vertical-center;
      @extend %fileuploader-animation;

      & + .fileuploader-action {
        margin-left: $fileuploader-distance;
      }

      &:hover {
        color: $fileuploader-color-active;
      }

      &:active {
        color: darken($fileuploader-color-active, 4%);
      }

      i {
        @extend %fileuploader-icon;
        display: inline-block;
        width: 100%;
        height: 100%;
        font-size: $fileuploader-item-actionSize;
        line-height: $fileuploader-item-actionSize;
        text-align: center;
        border-radius: 50%;
        @extend %fileuploader-transition-2;
      }

      &.fileuploader-action-remove i {
        color: #fff;
        font-size: 14px;
        background: $fileuploader-color-error;
        box-shadow: -1px 1px 6px rgba($fileuploader-color-error, 0.8);
        text-shadow: none;

        &:after {
          content: "\e908";
        }

        &:hover {
          background-color: lighten($fileuploader-color-error, 4%);
        }
        &:active {
          background-color: darken($fileuploader-color-error, 4%);
        }
      }
      &.fileuploader-action-download i:after {
        content: "\e902";
      }
      &.fileuploader-action-start i:after {
        content: "\e907";
      }
      &.fileuploader-action-sort i:after {
        content: "\e903";
      }
      &.fileuploader-action-edit i:after {
        content: "\e90c";
      }
      &.fileuploader-action-retry i:after {
        content: "\e904";
      }
      &.fileuploader-action-settings i:after {
        content: "\e905";
      }
      &.fileuploader-action-preview i:after {
        content: "\e90d";
      }
      &.fileuploader-action-success i {
        color: #fff;
        font-size: 14px;
        background: $fileuploader-color-success;
        box-shadow: -1px 1px 6px rgba($fileuploader-color-success, 0.8);
        text-shadow: none;

        &:after {
          content: "\e90b";
        }

        &:hover {
          &:after {
            content: "\e908";
          }

          background: $fileuploader-color-error;
          box-shadow: -1px 1px 6px rgba($fileuploader-color-error, 0.8);
        }
        &:active {
          &:after {
            content: "\e908";
          }
          background-color: darken($fileuploader-color-error, 4%);
        }
      }
    }

    &.file-has-popup span.fileuploader-action-popup {
      display: block;
    }

    .fileuploader-progressbar {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 4px;
      border-radius: $fileuploader-borderRadius;
      overflow: hidden;

      .bar {
        position: absolute;
        left: 0;
        top: 0;
        width: 0%;
        height: 100%;
        border-radius: $fileuploader-borderRadius;
        background: $fileuploader-color-active;
        @extend %fileuploader-transition-2;
      }
    }

    .progress-bar2 {
      .fileuploader-progressbar {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        @extend %user-select-none;

        .bar {
          position: absolute;
          left: 0;
          top: 0;
          width: 0%;
          height: 100%;
          border-radius: 0;
          background: rgba(104, 125, 219, 0.08);
          @extend %fileuploader-transition-2;
        }
      }

      span {
        position: absolute;
        @extend %transform-to-vertical-center;
        right: $fileuploader-distance;
        color: rgba(151, 159, 184, 0.16);
        font-size: 48px;
      }
    }

    &.sorting {
      background: $fileuploader-background;
      border-radius: $fileuploader-borderRadius;
      opacity: 0.8;
      box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
      z-index: 799;
    }

    &.sorting,
    &.sorting .fileuploader-item-image img,
    &.sorting .fileuploader-item-image canvas,
    &.sorting .fileuploader-item-icon,
    &.sorting .fileuploader-action {
      -webkit-animation: none;
      animation: none;
    }
  }

  .fileuploader-sorter-placeholder {
    background: rgba(0, 0, 0, 0.03);
    margin: 0;
    padding: 0;
    -webkit-animation: none;
    animation: none;
  }
}

/* popup */
.fileuploader-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #191D1E;
  z-index: $fileuploader-popup-zIndex;
  @extend %fileuploader-animation;
  animation-duration: 0.4s;

  &.loading:after {
    @extend %fileuploader-loader;
    @extend %transform-to-center;
    width: 48px;
    height: 48px;
    z-index: 8;
  }

  .fileuploader-popup-preview {
    position: relative;
    width: 100%;
    height: 100%;
    padding-right: $fileuploader-popup-contentWidth;
    overflow: auto;
    z-index: 2;
  }

  .fileuploader-popup-node {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    height: 100%;
    min-width: 20px;
    min-height: 20px;
    vertical-align: top;
    border-radius: 2px;
    padding: $fileuploader-popup-padding $fileuploader-popup-move-width;
    overflow: hidden;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    @extend %fileuploader-animation;

    &.image > img,
    &.video > video,
    &.audio > audio,
    &.astext > div {
      max-width: 100%;
      max-height: 100%;
      margin: 0;
      padding: 0;
      color: #47525d;
      background: #fafafa;
      -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
      border-radius: 2px;
    }

    &.audio audio {
      width: 450px;
    }

    &.astext div {
      max-width: 992px;
      padding: 20px;
      margin: 0 auto;
      font-size: 14px;
      line-height: 16px;
      text-align: left;
      overflow-y: auto;
      white-space: pre-wrap;
    }

    .fileuploader-popup-file-icon {
      position: relative;
      display: inline-block;
      width: 80px;
      height: 80px;
      background: url("data:image/svg+xml;base64,#{$icon-file}") no-repeat center;
      background-size: contain;
      @extend %user-select-none;

      div {
        position: absolute;
        bottom: 14px;
        left: 0;
        padding: 4px 6px;
        border-radius: 4px;
        color: #fff;
        max-width: 100%;
        word-wrap: break-word;

        &.is-bright-color {
          color: #888;
        }

        i {
          text-transform: uppercase;
          font-style: normal;
          font-weight: bold;
          white-space: nowrap;
        }
      }
    }
  }

  .fileuploader-popup-content {
    position: absolute;
    top: 0;
    right: 0;
    background: #fff;
    display: inline-block;
    width: $fileuploader-popup-contentWidth;
    min-height: 100%;
    padding: $fileuploader-popup-padding;
    text-align: left;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        display: block;
        padding: 0;
        margin: 0;
        padding-bottom: 16px;
        width: 100%;

        span {
          display: block;
          color: $fileuploader-popup-content-mutedColor;
          font-size: 14px;
        }

        h5 {
          margin: 4px 0 0;
          color: $fileuploader-popup-content-color;
          font-size: 14px;
          font-weight: bold;
          word-wrap: break-word;
        }
      }
    }

    .fileuploader-popup-meta {
      padding: 0 0 8px;
      margin: 0 0 8px;
      border-bottom: 1px solid #EAEAF0;
    }

    .fileuploader-popup-tools {
      padding: 0;
      margin: 0 $fileuploader-distance*-1;

      li {
        display: inline-block;
        width: auto;

        a {
          display: inline-block;
          padding: $fileuploader-distance;
          cursor: pointer;
          text-align: center;
          text-decoration: none;
          @extend %user-select-none;

          i {
            display: inline-block;
            font-size: 24px;
            margin-bottom: 6px;
            color: $fileuploader-popup-content-color;

            &:after {
              @extend %fileuploader-icon;
            }
          }

          i,
          span {
            @extend %fileuploader-transition-1;
          }

          &:hover {
            i, span {
              color: $fileuploader-color-active;
            }
          }

          &[data-action="remove"] i:after {
            content: "\e906";
          }

          &[data-action="crop"] i:after {
            content: "\e901";
          }

          &[data-action="rotate-cw"] i:after {
            content: "\e904";
          }
        }
      }
    }
    .fileuploader-popup-buttons {
      margin: $fileuploader-distance 0;
    }

    .fileuploader-popup-button {
      @extend %fileuploader-button-default;

      & + .fileuploader-popup-button {
        margin-left: 20px;
      }

      &.button-success {
        @extend %fileuploader-button-theme;
      }
    }
  }

  .fileuploader-popup-move {
    position: absolute;
    display: none;
    width: $fileuploader-popup-move-width;
    height: 100%;
    font-size: 24px;
    bottom: 0;
    left: 0;
    color: #fff;
    opacity: 0.4;
    cursor: pointer;
    z-index: 2;
    @extend %fileuploader-transition-2;

    &:hover {
      opacity: 1;
    }

    &:after {
      position: absolute;
      @extend %fileuploader-icon;
      @extend %transform-to-center;
    }

    &[data-action="prev"] {
      &:after {
        content: "\e909";
      }
    }

    &[data-action="next"] {
      left: auto;
      right: $fileuploader-popup-contentWidth;

      &:after {
        content: "\e90a";
      }
    }

  }

  .fileuploader-popup-has-arrows .fileuploader-popup-move {
    display: inline-block;
  }

  .fileuploader-cropper,
  .fileuploader-cropper * {
    @extend %user-select-none;
  }

  .fileuploader-cropper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(17, 20, 27, .65);
    z-index: 9;

    .fileuploader-cropper-area {
      position: absolute;
      left: 0;
      top: 0;
      width: 0;
      height: 0;
      z-index: 2;

      &.has-grid:before,
      &.has-grid:after {
        content: '';
        position: absolute;
        border: 1px solid rgba(250, 250, 250, 0.8);
        opacity: 0;
        -webkit-transition: all 0.4s ease;
        transition: all 0.4s ease;
        z-index: 1;
      }

      &.has-grid:before {
        top: 0;
        left: 50%;
        height: 100%;
        width: 34%;
        border-top: 0;
        border-bottom: 0;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
      }

      &.has-grid:after {
        top: 50%;
        left: 0;
        height: 34%;
        width: 100%;
        border-left: 0;
        border-right: 0;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
      }

      &.has-grid.moving:before,
      &.has-grid.moving:after {
        opacity: 1;
      }

      .point {
        position: absolute;
        width: $fileuploader-cropper-pointWidth;
        height: $fileuploader-cropper-pointWidth;
        z-index: 3;

        &:after {
          content: '';
          width: $fileuploader-cropper-pointWidth/2;
          height: $fileuploader-cropper-pointWidth/2;
          position: absolute;
          background: #fff;
          box-shadow: 0 0 6px rgba(0, 0, 0, 0.4);
          border-radius: 50%;
          @extend %transform-to-center;
        }

        &.point-a {
          top: $fileuploader-cropper-pointWidth/2 * -1;
          left: $fileuploader-cropper-pointWidth/2 * -1;
          cursor: nw-resize;
        }
        &.point-b {
          top: $fileuploader-cropper-pointWidth/2 * -1;
          left: 50%;
          margin-left: $fileuploader-cropper-pointWidth/2 * -1;
          cursor: n-resize;
        }
        &.point-c {
          top: $fileuploader-cropper-pointWidth/2 * -1;
          right: $fileuploader-cropper-pointWidth/2 * -1;
          cursor: ne-resize;
        }
        &.point-d {
          top: 50%;
          right: $fileuploader-cropper-pointWidth/2 * -1;
          margin-top: $fileuploader-cropper-pointWidth/2 * -1;
          cursor: w-resize;
        }
        &.point-e {
          bottom: $fileuploader-cropper-pointWidth/2 * -1;
          right: $fileuploader-cropper-pointWidth/2 * -1;
          cursor: nw-resize;
        }
        &.point-f {
          bottom: $fileuploader-cropper-pointWidth/2 * -1;
          left: 50%;
          margin-left: $fileuploader-cropper-pointWidth/2 * -1;
          cursor: s-resize;
        }
        &.point-g {
          bottom: $fileuploader-cropper-pointWidth/2 * -1;
          left: $fileuploader-cropper-pointWidth/2 * -1;
          cursor: sw-resize;
        }
        &.point-h {
          left: $fileuploader-cropper-pointWidth/2 * -1;
          top: 50%;
          margin-top: $fileuploader-cropper-pointWidth/2 * -1;
          cursor: w-resize;
        }
      }

      .area-move {
        position: absolute;
        width: 95%;
        height: 95%;
        @extend %transform-to-center;
        z-index: 2;
        cursor: move;
      }

      .area-image {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 100%;

        img {
          width: auto;
          height: auto;
          max-width: none;
          max-height: none;
          position: absolute;
          left: 0;
          top: 0;
          background: #fff;
          -webkit-transform-origin: top left;
          transform-origin: top left;
        }
      }
    }
  }
}

/* responsive */
@media all and (max-width: 768px) {
  .fileuploader-popup {
    .fileuploader-popup-preview {
      padding-right: 0;
    }

    .fileuploader-popup-move {
      height: $fileuploader-popup-move-width;
      font-size: 18px;
      top: 0;
      left: calc(50% - #{$fileuploader-popup-move-width});

      &[data-action="next"] {
        right: auto;
        left: 50%;
      }
    }

    .fileuploader-popup-node,
    .fileuploader-popup-content {
      position: relative;
      display: block;
      width: 100%;
      max-width: initial;
      min-height: initial;
      height: auto;
      text-align: center;
      margin: 0;
    }

    .fileuploader-popup-node {
      padding: $fileuploader-popup-padding;
    }

    .fileuploader-popup-has-arrows .fileuploader-popup-node {
      padding-top: $fileuploader-popup-move-width + $fileuploader-popup-padding;
    }

    .fileuploader-popup-node.audio {
      width: 100%;
      top: 0;
      transform: none;
    }

    .fileuploader-popup-content .fileuploader-popup-meta {
      margin-top: 24px;

      li {
        display: inline-block;
        width: auto;
        max-width: 100%;
        text-align: left;
        padding-right: 36px;

        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
}

/* animation */
@-webkit-keyframes fileuploaderFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fileuploaderFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}